<template>
  <div class="menu-bar">
    <!-- 欢迎标语 -->
    <div class="menu-bar-header">
      <div>{{ accountInfo.account }}</div>
      <div>欢迎使用积分服务中心</div>
    </div>

    <!-- 菜单 -->
    <el-menu
      background-color="#2a3746"
      text-color="#fff"
      active-text-color="#fff"
      :default-active="defaultActive"
      router>
      <el-menu-item v-for="item in accountInfo.menuList" :key="item.name" :index="`/${item.name}`">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{ item.label }}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'MenuBar',
  computed: {
    ...mapGetters({ accountInfo: 'getAccountInfo' }),

    // 激活的一级菜单
    defaultActive () {
      const index = this.$route.path.slice(1)
      return index ? '/' + index.split('/')[0] : ''
    }
  }
}
</script>

